



.header-section {
  max-width: 375px;
  margin: 0 auto;
  padding: 25px 20px;
  display: flex;
  position: relative;
}

.header-logo {
  color: #111;
  letter-spacing: -.42px;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 18px;
  position: relative;
}

.header-logo .simply-logo {
  font-style: italic;
}

.header-logo .chocolate-logo {
  width: 21px;
  height: 21px;
  position: absolute;
}

.navigation-header-bar {
  color: #fff;
  letter-spacing: -.28px;
  flex-direction: column;
  gap: 24px;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
  display: flex;
}

.header-social-icons {
  color: #fff;
  width: 24px;
  height: 24px;
}

.header-social-icons:hover {
  color: #fd9222;
}

.header-modal-button {
  background-color: #fff;
  border: none;
}

.burger-button {
  width: 28px;
  height: 28px;
  cursor: pointer;
  position: absolute;
  top: 20px;
  right: 20px;
}

.header-modal {
  width: 220px;
  background-color: #1e1823;
  flex-direction: column;
  gap: 460px;
  padding-top: 136px;
  padding-bottom: 20px;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
}

.header-icons-section {
  gap: 18px;
  padding-left: 20px;
  display: flex;
}

.close-burger-btn {
  color: #fff;
  cursor: pointer;
  width: 28px;
  height: 28px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: absolute;
  top: 20px;
  right: 20px;
}

.header-modal-is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: all .25s;
}















